
#header {
  padding-top: 1%;
  padding-bottom: 1%;
  margin-bottom: 1%;
  border-bottom-style: inset;
}

.hovered-container {
  position: relative;
}
.hovered-image {
  opacity: 1;
}
.hovered-popup {
  opacity: 0;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 33%;
  padding-top: 5%;
  padding-left: 5%;
  background-color: dimgray;
}
.hovered-popup .title {
  color: white;
}
.hovered-popup .subtitle {
  color: white;
}
.hovered-container:hover .hovered-image {
  opacity: 0.3;
}
.hovered-container:hover .hovered-popup {
  opacity: 1;
}
